import React from 'react';
import PropTypes from 'prop-types';
import IconCircle from 'react-icons/lib/fa/circle-o';
import IconClose from 'react-icons/lib/fa/close';
import IconRefresh from 'react-icons/lib/fa/refresh';

const Score = ({
  correct,
  wrong,
  genre,
  onRestart,
}) => (
  <div className="popup">
    <div className="box">
      <div className="box-header">
        <h3 className="title">{genre}</h3>
      </div>
      <div className="box-inner">
        <div className="group">
          <strong className="topic">
            <IconCircle />
          </strong>
          <span className="inner">{correct}</span>
        </div>
        <div className="group">
          <strong className="topic">
            <IconClose />
          </strong>
          <span className="inner">{wrong}</span>
        </div>
      </div>
      <div className="box-footer btn-flex">
        <button className="btn-lg" type="button" onClick={onRestart}>
          <IconRefresh className="mr" />
          <span>Restart</span>
        </button>
      </div>
    </div>
  </div>
);

Score.propTypes = {
  correct: PropTypes.number.isRequired,
  wrong: PropTypes.number.isRequired,
  genre: PropTypes.string.isRequired,
  onRestart: PropTypes.func.isRequired,
};

export default Score;



