import React from 'react';
import IPropTypes from 'react-immutable-proptypes';
import IconYoutube from 'react-icons/lib/fa/youtube';
import IconSpotify from 'react-icons/lib/fa/spotify';

const Url = ({
  url,
}) => {
  const spotify = url.get('spotify');
  const hasSpotify = spotify && spotify.length > 0;
  const youtube = url.get('youtube');
  const hasYoutube = youtube && youtube.length > 0;

  return (
    <li className="item">
      <div className="topic list-pull-8">Link</div>
      <div className="inner">
        <div className="btn-group">
          {hasSpotify &&
            (
              <a className="btn-bg" target="_blank" title="Spotify Link" rel="noopener nofollow noreferer" href={`https://open.spotify.com/track/${spotify}`}>
                <IconSpotify />
              </a>
            )
          }
          {hasYoutube &&
            (
              <a className="btn-bg" target="_blank" title="Youtube Link" rel="noopener nofollow noreferer" href={`https://youtu.be/${youtube}`}>
                <IconYoutube />
              </a>
            )
          }
        </div>
      </div>
    </li>
  );
};

Url.propTypes = {
  url: IPropTypes.map.isRequired,
};

export default Url;



