import React from 'react';
import IconRepo from 'react-icons/lib/go/mark-github';

import {
  website,
  repo,
} from '@/config/author';

const Foot = () => (
  <div className="foot frame-item muted">
    <a href={repo} target="_blank" rel="noopener noreferrer" title="Repostion of MUTCH" className="mr">
      <IconRepo />
    </a>
    <a href={website} target="_blank" rel="noopener noreferrer" title="Personal Website">Erozak</a>
    &nbsp;&copy;&nbsp;
    <time alt="2017">2017</time>
  </div>
);

export default Foot;



