import React from 'react';
import PropTypes from 'prop-types';
import IPropTypes from 'react-immutable-proptypes';
import IconExternal from 'react-icons/lib/fa/external-link-square';
import IconCircle from 'react-icons/lib/fa/circle-o';
import IconClose from 'react-icons/lib/fa/close';

const QuestionItem = ({
  index,
  result,
  title,
  artists,
  onPopup,
}) => {
  let resultIcon;
  let resultClass;

  if (result !== undefined) {
    resultIcon = result
      ? <IconCircle className="icon" />
      : <IconClose className="icon" />;
    resultClass = result ? 'order correct' : 'order wrong';
  } else {
    resultClass = 'order';
  }

  return (
    <li className="item box-order">
      <div className={resultClass}>{resultIcon}</div>
      <div className="box">
        <div className="box-header">
          <h4 className="title">{title}</h4>
        </div>
        <div className="box-inner">{artists.join(', ')}</div>
      </div>
      <button className="box-btn" onClick={onPopup(index)}>
        <IconExternal />
      </button>
    </li>
  );
};

QuestionItem.propTypes = {
  index: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  artists: IPropTypes.list.isRequired,
  onPopup: PropTypes.func.isRequired,
  result: PropTypes.bool,
};

QuestionItem.defaultProps = {
  result: undefined,
};

export default QuestionItem;



