import React from 'react';
import PropTypes from 'prop-types';
import IconExternal from 'react-icons/lib/fa/external-link-square';

import { excelURL } from '@/config/googleAppScript/';

const Init = ({
  onMutchExcelChange,
  onMutchInit,
  excel,
}) => (
  <div className="dashboard">
    <div className="form-group">
      <input
        type="text"
        className="inp-block"
        onChange={onMutchExcelChange}
        placeholder="Excel ID"
        value={excel}
      />
    </div>
    <div className="flex-sb">
      <a href={excelURL + excel} className="btn" rel="noreferrer nofollow" title="Link to the Google Spread Sheet" target="_blank">
        <IconExternal />
      </a>
      <button className="btn" type="button" onClick={onMutchInit}>Get Datas</button>
    </div>
  </div>
);

Init.propTypes = {
  onMutchExcelChange: PropTypes.func.isRequired,
  onMutchInit: PropTypes.func.isRequired,
  excel: PropTypes.string.isRequired,
};

export default Init;



