import React from 'react';
import IPropTypes from 'react-immutable-proptypes';
import ShortId from 'shortid';

const Alias = ({
  alias,
}) => {
  const aliasValid = alias.filter(val => val.length > 0);
  const aliasSize = aliasValid.size;
  const topicClass = (aliasSize > 1) ? 'topic list-pull-8' : 'topic';
  const items = (aliasSize > 1)
    ? aliasValid.map(val => (
      <div key={ShortId.generate()} className="tag">{val}</div>
    ))
    : aliasValid.get(0);

  return (
    <li className="item">
      <div className={topicClass}>Alias</div>
      <div className="item">{items}</div>
    </li>
  );
};

Alias.propTypes = {
  alias: IPropTypes.list.isRequired,
};

export default Alias;



